const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			params: {
				page: 1,
				size: 1000
			},
			list: []
		}
	},
	//初始化
	onLoad() {
		this.getList();
		wx.hideShareMenu()
	},
	// 页面显示
	onShow() {},
	onShareAppMessage: function () {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/index/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	methods: {
		//获取提现列表
		getList() {
			util.request(api.getCashOutList,this.params,'post').then(res=>{
				if(res.code == 0) {
					this.list = res.data.list;
				}
			})
		},
		//取消提现
		cancelBtn(id) {
			wx.showLoading({
				mask: true,
			})
			util.request(api.cashOutCancel,{id:id},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					wx.showToast({
						icon: 'none',
						title: '取消成功'
					})
					this.getList();
				}
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
		
	},
	filters:{
		formatTimes(times) {
			return util.formatTime(times)
		}
	}
}
